</template>
<script>
    import {getRequest} from '../utils/api'
    export default {
        methods: {
            finditem(){
                getRequest("/item/getitembyname/?name="+this.input).then(resp => {
                    if (resp.status == 200) {
                        // eslint-disable-next-line no-debugger,no-undef
                        this.tableData = resp.data.data.content
                    }

                });
            },
            add() {
                this.$router.push({
                    path: `/addpatch/`+this.id,
                });
            },
            changePage(page) {
                this.page = page
                getRequest("/user//getuser/" + this.page + "/10").then(resp => {
                    if (resp.status == 200) {
                        // eslint-disable-next-line no-debugger
                        debugger;
                        this.tableData = resp.data.data.content
                    }

                });
            },
            returntop(){
                this.$router.push({
                    path: `/normalitem`,
                });
            },
            downloadpatch(id) {

                this.id = id;
                getRequest("/patch/downloadpatch/"+this.id).then(resp => {
                    if (resp.status == 200) {
                        // eslint-disable-next-line no-debugger,no-undef
                        debugger
                        if(resp.data.code == 0){
                            window.location.href = resp.data.data
                        }else{
                            alert("")
                        }

                    }

                });
            },
            updatepatchentry(id) {
                this.id = id;
                // eslint-disable-next-line no-debugger
                this.$router.push({
                    path: '/patchentry/'+this.id,
                });
                // eslint-disable-next-line no-console
                console.log(id)
            },
        },
        mounted() {
            this.id = this.id = this.$route.params.id,
            getRequest("/patch/getpatch/" + this.page + "/10/"+this.$route.params.id).then(resp => {
                if (resp.status == 200) {
                    // eslint-disable-next-line no-debugger
                    debugger;
                    if(resp.data.data.content.closetype){
                        resp.data.data.content.closetype =""
                    }else{
                        resp.data.data.content.closetype =""
                    }
                    this.tableData = resp.data.data.content
                    this.total = resp.data.data.totalElements
                }

            });
        },

        data() {
            return {
                formatBoolean: function (row, column, cellValue) {
                    var ret = ''  //
                    if (cellValue) {
                        ret = ""  //
                    } else {
                        ret = ""
                    }
                    return ret;
                },
                total: 0,
                page: 1,
                id: "",
                tableData: [

                ],
                input: ''
            }
        }
    }
</script>

