<template xmlns:v>
    <div>
        <el-input v-model="input" placeholder=""></el-input>
        <el-button round @click="finditem"></el-button>
       <!-- <el-button round @click="add"></el-button>-->
        <el-table
                :data="tableData"
                stripe
                style="width: 100%">
            <el-table-column type="index"></el-table-column>
            <el-table-column
                    prop="id"
                    label=""
                    width="120">
            </el-table-column>
            <el-table-column
                    prop="name"
                    label=""
                    width="120">
            </el-table-column>
            <el-table-column
                    prop="remarks"
                    label=""
                    width="120">
            </el-table-column>
            <el-table-column
                    prop="createtime"
                    label=""
                    width="120">
            </el-table-column>
            <el-table-column
                    prop="endtime"
                    label=""
                    width="120">
            </el-table-column>
            <!--<el-table-column
                    :formatter="formatBoolean"
                    prop="closetype"
                    label=""
                    width="120">
            </el-table-column>-->
            <el-table-column
                    prop="userid"
                    label=""
                    width="120">
            </el-table-column>
            <el-table-column
                    prop="change"
                    label="">

                <template slot-scope="page">
                    <!--<el-button round @click="updateuser(page.row.id)"></el-button>
                    <el-button round @click="deleteuser(page.row.id)"></el-button>-->
                    <el-button round @click="addpatch(page.row.id)"></el-button>
                    <!--<el-button v-if="closetype" round @click="closetype(page.row.id)">/</el-button>-->
                </template>
                <!--<el-button round></el-button>-->


            </el-table-column>

        </el-table>
        <el-pagination
                background
                layout="prev, pager, next"
                :total="total"
                :current-page="this.page"
                v-on:current-change="changePage">
        </el-pagination>

    </div>

</template>
<script>
    import {getRequest} from '../utils/api'

    export default {
        methods: {
            finditem(){
                getRequest("/item/getitembyname/?name="+this.input).then(resp => {
                    if (resp.status == 200) {
                        // eslint-disable-next-line no-debugger,no-undef
                        this.tableData = resp.data.data.content
                    }

                });
            },
            add() {
                this.$router.push({
                    path: `/additem`,
                });
            },
            changePage(page) {
                this.page = page
                getRequest("/user/getuser/" + this.page + "/10").then(resp => {
                    if (resp.status == 200) {
                        // eslint-disable-next-line no-debugger
                        debugger;
                        this.tableData = resp.data.data.content
                    }

                });
            },

            addpatch(id){
                this.id = id;
                // eslint-disable-next-line no-debugger
                this.$router.push({
                    path: 'patch/'+this.id,
                });
                // eslint-disable-next-line no-console
                console.log(id)
            },
            closetype(id) {
                this.id = id;
                this.$confirm('', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    // eslint-disable-next-line no-console
                    console.log(id)
                    getRequest("/item/closeitem/" + this.id).then(resp => {
                        if (resp.status == 200) {
                            // eslint-disable-next-line no-debugger
                            debugger;
                            this.$router.go(0);
                        }

                    });


                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });// eslint-disable-next-line no-console
            },// eslint-disable-next-line no-console
        },
        mounted() {
            getRequest("/normal/getitme/" + this.page + "/10").then(resp => {
                if (resp.status == 200) {
                    // eslint-disable-next-line no-debugger
                    debugger;
                    if(resp.data.data.content.closetype){
                        resp.data.data.content.closetype =""
                    }else{
                        resp.data.data.content.closetype =""
                    }
                    this.tableData = resp.data.data.content
                    this.total = resp.data.data.totalElements
                }

            });
        },

        data() {
            return {
                formatBoolean: function (row, column, cellValue) {
                    var ret = ''  //
                    if (cellValue) {
                        ret = ""  //
                    } else {
                        ret = ""
                    }
                    return ret;
                },
                total: 0,
                page: 1,
                id: "",
                tableData: [],
                input: ''
            }
        }
    }
</script>

