<template xmlns:v>
    <div>
        <el-input v-model="input" placeholder=""></el-input>
        <el-button round @click="finduser"></el-button>
        <el-table
                @selection-change="changeFun"
                :data="tableData"
                stripe
                style="width: 100%">
            <el-table-column
                    type="selection"
                    width="55">
            </el-table-column>
            <el-table-column type="index"></el-table-column>
            <el-table-column
                    prop="id"
                    label=""
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="name"
                    label=""
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="department"
                    label=""
                    width="180">
            </el-table-column>


        </el-table>
        <el-button round @click="adduseritem"></el-button>
        <el-pagination
                background
                layout="prev, pager, next"
                :total="total"
                :current-page="this.page"
                v-on:current-change="changePage">
        </el-pagination>

    </div>

</template>
<script>
    import {getRequest} from '../utils/api'
    import {postRequest} from '../utils/api'

    export default {
        created(){
            this.id = this.$route.params.id
        },
        methods: {
            changeFun(val){
                // eslint-disable-next-line no-debugger
                debugger;
                this.multipleSelection = val;
                // eslint-disable-next-line no-console
                console.log(this.multipleSelection)
            },
            finduser(){
                getRequest("/user/getuserbyname/?name="+this.input).then(resp => {
                    if (resp.status == 200) {
                        // eslint-disable-next-line no-debugger,no-undef
                        this.tableData = resp.data.data.content
                    }

                });
            },
            changePage(page) {
                this.page = page
                getRequest("/user//getuser/" + this.page + "/10").then(resp => {
                    if (resp.status == 200) {
                        // eslint-disable-next-line no-debugger
                        debugger;
                        this.tableData = resp.data.data.content
                    }

                });
            },
            adduseritem(){
                var _this = this;
                this.loading = true;
                postRequest('/item/adduser', {

                    user: JSON.stringify(this.multipleSelection),
                    itemid:this.id,
                }).then(resp=> {
                    // eslint-disable-next-line no-debugger
                    _this.loading = false;
                    var data = resp.data;
                    if (resp.status == 200) {

                        var code = data.code;
                        if (code == 0) {
                            _this.$router.replace({path: '/itemuserlist/'+this.id});
                        } else {
                            _this.$alert('!', '!');
                        }
                    } else {
                        //
                        _this.$alert('!', '!');
                    }
                });
            },// eslint-disable-next-line no-console
        },
        mounted() {
            getRequest("/item/getusernoitem/?id=" + this.id).then(resp => {
                if (resp.status == 200) {
                    // eslint-disable-next-line no-debugger
                    debugger;
                    this.tableData = resp.data.data.content
                    this.total = resp.data.data.totalElements
                }

            });
        },

        data() {
            return {
                total: 0,
                page: 1,
                id: "",
                tableData: [],
                multipleSelection:[],
                input: ''
            }
        }
    }
</script>

