<template>
    <el-form ref="form" :model="user" label-width="70px">
        <el-form-item label="">
            <el-input v-model="user.name"></el-input>
        </el-form-item>
        <el-form-item label="">
            <el-input v-model="user.password"></el-input>
        </el-form-item>
        <el-form-item label="">
            <el-input v-model="user.phone"></el-input>
        </el-form-item>
        <el-form-item label="">
            <el-select v-model="user.department" placeholder="">
                <el-option label="" value="0"></el-option>
                <el-option label="" value="1"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="">
            <el-select v-model="user.power" placeholder="">
                <el-option label="" value="0"></el-option>
                <el-option label="" value="1"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" @click="onSubmit"></el-button>
            <el-button @click="qx"></el-button>
        </el-form-item>
    </el-form>

</template>
<script>
    import {postRequest} from '../utils/api'
    import {getRequest} from '../utils/api'
    export default {
        data() {
            return {
                user: {
                    name: '',
                    password: '',
                    phone: '',
                    item: '',
                    department: ''
                },
                id:""
            }
        },
        created() {
          var _this = this;
          this.id = this.$route.params.id,
                  getRequest("/user/getuserbyid/"+_this.id).then(function (msg) {
                    _this.user = msg.data.data;
                    // eslint-disable-next-line no-unused-vars
                  }, function (msg) {
                    _this.currentUserName = '';
                  });

        },
        methods: {
            onSubmit() {
                var _this = this;
                postRequest('/user/updateuser', _this.user).then(resp => {
                    // eslint-disable-next-line no-debugger
                    _this.loading = false;
                    var data = resp.data;
                    if (resp.status == 200) {
                        var code = data.data;
                        if (code) {
                          _this.$alert('!', '!');
                          this.$router.push({
                            path: `/userlist`,
                          });
                        } else {
                            _this.$alert('!', '!');
                        }
                    } else {
                        //
                        _this.$alert('!', '!');
                    }
                });
            },
            qx() {
                this.$router.push({
                    path: `/userlist`,
                });

            }
        }
    }
</script>