<template xmlns:v>
    <div>
        <el-input v-model="input" placeholder=""></el-input>
        <el-button round @click="finduser"></el-button>
        <el-button round @click="add"></el-button>
        <el-table
                :data="tableData"
                stripe
                style="width: 100%">
            <el-table-column type="index"></el-table-column>
            <el-table-column
                    prop="id"
                    label=""
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="name"
                    label=""
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="password"
                    label=""
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="phone"
                    label=""
                    width="180">
            </el-table-column>
            <el-table-column
                    :formatter="formatBoolean"
                    prop="power"
                    label=""
                    width="180">
            </el-table-column>
            <el-table-column
                    :formatter="formatBoolean1"
                    prop="department"
                    label=""
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="change"
                    label="">

                <template slot-scope="page">
                    <el-button round @click="updateuser(page.row.id)"></el-button>
                    <el-button round @click="deleteuser(page.row.id)"></el-button>
                </template>
                <!--<el-button round></el-button>-->


            </el-table-column>

        </el-table>
        <el-pagination
                background
                layout="prev, pager, next"
                :total="total"
                :current-page="this.page"
                v-on:current-change="changePage">
        </el-pagination>

    </div>

</template>
<script>
    import {getRequest} from '../utils/api'

    export default {
        methods: {
            finduser(){
                getRequest("/user/getuserbyname/?name="+this.input).then(resp => {
                    if (resp.status == 200) {
                        // eslint-disable-next-line no-debugger,no-undef
                        this.tableData = resp.data.data.content
                    }

                });
            },
            add() {
                this.$router.push({
                    path: `/adduser`,
                });
            },
            changePage(page) {
                this.page = page
                getRequest("/user//getuser/" + this.page + "/10").then(resp => {
                    if (resp.status == 200) {
                        // eslint-disable-next-line no-debugger
                        debugger;
                        this.tableData = resp.data.data.content
                    }

                });
            },
            deleteuser(id) {
                this.id = id;
                this.$confirm('', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    // eslint-disable-next-line no-console
                    console.log(id)
                    getRequest("/user/deleteuser/" + this.id).then(resp => {
                        if (resp.status == 200) {
                            // eslint-disable-next-line no-debugger
                            debugger;
                            this.$router.go(0);
                        }

                    });


                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });// eslint-disable-next-line no-console
            },

            updateuser(id) {
                this.id = id;
                // eslint-disable-next-line no-debugger
                this.$router.push({
                    path: 'updateuser/'+this.id,
                });
                // eslint-disable-next-line no-console
                console.log(id)

            }// eslint-disable-next-line no-console
        },
        mounted() {
            getRequest("/user/getuser/" + this.page + "/10").then(resp => {
                if (resp.status == 200) {
                    // eslint-disable-next-line no-debugger
                    debugger;
                    this.tableData = resp.data.data.content
                    this.total = resp.data.data.totalElements
                }

            });
        },

        data() {
            return {
                formatBoolean: function (row, column, cellValue) {
                    var ret = ''  //
                    if (cellValue == 0) {
                        ret = ""  //
                    } else {
                        ret = ""
                    }
                    return ret;
                },
                formatBoolean1: function (row, column, cellValue) {
                    var ret = ''  //
                    if (cellValue == 0) {
                        ret = ""  //
                    } else {
                        ret = ""
                    }
                    return ret;
                },
                total: 0,
                page: 1,
                id: "",
                tableData: [],
                input: ''
            }
        }
    }
</script>

