<template>
    <el-form ref="user" status-icon :rules="rules" :model="user" label-width="70px">
        <el-form-item label="" prop="name">
            <el-input v-model="user.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
            <el-input v-model="user.password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="phone">
            <el-input v-model="user.phone"></el-input>
        </el-form-item>
        <el-form-item label="" prop="department">
            <el-select v-model="user.department" placeholder="">
                <el-option label="" value="0"></el-option>
                <el-option label="" value="1"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="" prop="power">
            <el-select v-model="user.power" placeholder="">
                <el-option label="" value="0"></el-option>
                <el-option label="" value="1"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" @click="onSubmit('user')"></el-button>
            <el-button @click="qx"></el-button>
        </el-form-item>
    </el-form>

</template>
<script>
    import {postRequest} from '../utils/api'

    export default {
        data() {
            return {
                user: {
                    name: '',
                    password: '',
                    phone: '',
                    item: '',
                    department: ''
                },
                rules: {
                    name: [
                        {required: true, message: '', trigger: 'blur'},
                    ],
                    password: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    phone: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    department: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    power: [
                        { required: true, message: '', trigger: 'blur'}
                    ]

                }
            }
        },
        methods: {

            onSubmit(user) {
                this.$refs[user].validate((valid) => {
                    if (valid) {
                        var _this = this;
                        postRequest('/user/adduser', _this.user).then(resp => {
                            // eslint-disable-next-line no-debugger
                            _this.loading = false;
                            var data = resp.data;
                            if (resp.status == 200) {
                                _this.$alert('!', '!');
                                var code = data.data;
                                if (code) {
                                    _this.user={}
                                } else {
                                    _this.$alert('!', '!');
                                }
                            } else {
                                //
                                _this.$alert('!', '!');
                            }
                        });
                    } else {

                        return false;
                    }
                });

            },
            qx() {
                this.$router.push({
                    path: `/home`,
                });

            }
        }
    }
</script>