<template xmlns:v>
    <div>
        <el-input v-model="input" placeholder=""></el-input>
        <el-button round @click="finditem"></el-button>
        <el-button round @click="add"></el-button>
        <el-table
                :data="tableData"
                stripe
                style="width: 100%">
            <el-table-column type="index"></el-table-column>
            <el-table-column
                    prop="id"
                    label=""
                    width="120">
            </el-table-column>
            <el-table-column
                    prop="name"
                    label=""
                    width="120">
            </el-table-column>
            <el-table-column
                    prop="remarks"
                    label=""
                    width="120">
            </el-table-column>
            <el-table-column
                    prop="type"
                    label=""
                    width="120">
            </el-table-column>
            <el-table-column
                    prop="createtime"
                    label=""
                    width="120">
            </el-table-column>
            <el-table-column
                    prop="createuser"
                    label=""
                    width="120">
            </el-table-column>
            <el-table-column
                    prop="updatetime"
                    label=""
                    width="120">
            </el-table-column>
            <el-table-column
                    prop="updateuser"
                    label=""
                    width="120">
            </el-table-column>
            <el-table-column
                    prop="ischeck"
                    label=""
                    width="120">
            </el-table-column>
            <el-table-column
                    prop="change"
                    label="">

                <template slot-scope="page">
                    <el-button round @click="updateuser(page.row.id)"></el-button>
                    <el-button round @click="deletepatchentry(page.row.id)"></el-button>
                </template>


            </el-table-column>

        </el-table>
        <el-pagination
                background
                layout="prev, pager, next"
                :total="total"
                :current-page="this.page"
                v-on:current-change="changePage">
        </el-pagination>
        <el-button type="primary" round @click="createpatch"></el-button>
        <el-button type="primary" round @click="returntop"></el-button>
    </div>

</template>
<script>
    import {getRequest} from '../utils/api'

    export default {
        methods: {
            finditem() {
                getRequest("/item/getitembyname/?name=" + this.input).then(resp => {
                    if (resp.status == 200) {
                        // eslint-disable-next-line no-debugger,no-undef
                        this.tableData = resp.data.data.content
                    }

                });
            },
            add() {
                this.$router.push({
                    path: `/addpatchentry/` + this.$route.params.id,
                });
            },
            changePage(page) {
                this.page = page
                getRequest("/user//getuser/" + this.page + "/10").then(resp => {
                    if (resp.status == 200) {
                        // eslint-disable-next-line no-debugger
                        debugger;
                        this.tableData = resp.data.data.content
                    }

                });
            },
            createpatch() {
                getRequest("/patch/ceratepatch/" + this.$route.params.id).then(resp => {
                    if (resp.status == 200) {

                        // eslint-disable-next-line no-debugger
                        if (resp.data) {
                            alert("")
                        }


                    }

                });
            },
            deletepatchentry(id) {
                this.id = id;
                this.$confirm('', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    // eslint-disable-next-line no-console
                    console.log(id)
                    getRequest("/patchentry/deletepatchentry/" + this.id).then(resp => {
                        if (resp.status == 200) {
                            // eslint-disable-next-line no-debugger
                            debugger;
                            this.$router.go(0);
                        }

                    });


                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });// eslint-disable-next-line no-console
            },
            returntop() {
                this.$router.push({
                    path: `/patch/`+this.aid,
                });
            }


        },
        mounted() {
            getRequest("/patchentry/getpatchentry/" + this.page + "/10/" + this.$route.params.id).then(resp => {
                if (resp.status == 200) {
                    // eslint-disable-next-line no-debugger
                    debugger;

                    this.tableData = resp.data.data.content

                    this.total = resp.data.data.totalElements
                }

            });
            getRequest("/patchentry/getpatchid/" + this.$route.params.id).then(resp => {

                // eslint-disable-next-line no-debugger
                debugger;
                this.aid=resp.data.data.id

            }, function (msg) {
                console(msg)
            });
        },
        created() {

        },
        data() {
            return {
                formatBoolean: function (row, column, cellValue) {
                    var ret = ''  //
                    if (cellValue) {
                        ret = ""  //
                    } else {
                        ret = ""
                    }
                    return ret;
                },
                total: 0,
                aid:"",
                page: 1,
                id: "",
                tableData: [],
                input: ''
            }
        }
    }
</script>

