<template>
    <el-form ref="form" :model="item" label-width="70px">
        <el-form-item label="" prop="name">
            <el-input v-model="item.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="remarks">
            <el-input type="textarea" v-model="item.remarks"></el-input>
        </el-form-item>
        <el-form-item label="" prop="endtime">
            <el-date-picker type="date" placeholder="" v-model="item.endtime" format="yyyy  MM  dd "
                            value-format="yyyy-MM-dd" :picker-options="pickerOptions0"></el-date-picker>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" @click="onSubmit"></el-button>
            <el-button @click="qx"></el-button>
        </el-form-item>
    </el-form>

</template>
<script>
    import {postRequest} from '../utils/api'
    import {getRequest} from '../utils/api'
    export default {
        data() {
            return {
                item: {
                    name: '',
                    remarks: '',
                    endtime: '',

                },
                id:""
            }
        },
        created() {
          var _this = this;
          this.id = this.$route.params.id,
                  getRequest("/item/getitembyid/"+_this.id).then(function (msg) {
                    _this.item = msg.data.data;
                    // eslint-disable-next-line no-unused-vars
                  }, function (msg) {
                    _this.currentUserName = '';
                  });

        },
        methods: {
            onSubmit() {
                var _this = this;
                postRequest('/item/updateitem', _this.item).then(resp => {
                    // eslint-disable-next-line no-debugger
                    _this.loading = false;
                    var data = resp.data;
                    if (resp.status == 200) {
                        var code = data.data;
                        if (code) {
                          _this.$alert('!', '!');
                          this.$router.push({
                            path: `/itemlist`,
                          });
                        } else {
                            _this.$alert('!', '!');
                        }
                    } else {
                        //
                        _this.$alert('!', '!');
                    }
                });
            },
            qx() {
                this.$router.push({
                    path: `/itemlist`,
                });

            }
        }
    }
</script>